@font-face {
  font-family: Gilbert;
  src: url(../assets/gilbert-color.otf);
  font-weight: 700;
}

.wp-block-create-block-gutenpride {
  font-family: Gilbert, sans-serif;
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.wp-block-create-block-gutenpride > * {
  flex-basis: 50%;
}
